@import url("https://fonts.googleapis.com/css2?family=Inter&family=Itim&family=Jaldi&family=JetBrains+Mono&family=Righteous&family=Roboto+Mono:wght@200;400&family=Rock+Salt&family=Ropa+Sans:ital@0;1&display=swap");
body {
  background-color: #222222;
}

.main-page {
  width: 1440px;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  align-items: center;

  // **************************************
  .top-section {
    z-index: 10;
    width: 1440px;
    height: 100px;
    margin-top: 0px;
    background-color: #f5f5f5;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0px;
    background-color: #222222;
    box-shadow: 0px 2px 5px rgba(255, 255, 255, 0.1);
    .top-menu {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      .top-menu-item-box {
        display: none;
        width: 80px;
        height: 60px;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        box-shadow: 0px 2px 5px rgba(255, 255, 255, 0.1);
        position: absolute;
        right: 20px;
        p {
          color: #f26c4f;
          font-size: 30px;
        }
      }
      .top-menu-item-all {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        .top-menu-item-0-icon {
          display: none;
          color: #fff;
          &:hover {
            color: #f26c4f;
          }
        }
        .top-menu-item-2,
        .top-menu-item-3,
        .top-menu-item-4,
        .top-menu-item-5,
        .top-menu-item-6 {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 120px;
          height: 40px;
          background-color: #222222;
          cursor: pointer;
          border-radius: 5px;
          margin-left: 50px;
          &:hover {
            background-color: #f26c4f;
            color: #fff;
            box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
          }
          p {
            color: #fff;
            font-family: "Itim", cursive;
          }
        }
        .top-menu-item-1 {
          background-color: #222222;
          p {
            color: #fff;
            font-family: "Itim", cursive;
          }
        }
      }
    }
  }

  // ********************************************
  .main-section {
    display: flex;
    align-items: center;
    margin-top: 100px;
    .main-section-top-left {
      margin-top: -150px;
      margin-left: 100px;
      .left-bold {
        width: 500px;
        height: 100px;
        display: flex;
        // align-items: center;
        justify-content: left;
        flex-direction: column;
        // border: 1px solid #fff;
        .high-bold-1 {
          justify-content: left;
          font-family: "Itim", cursive;
          font-size: 25px;
          color: #fff;
          margin-top: -10px;
        }
        .high-bold-2 {
          justify-content: left;
          font-family: "Itim", cursive;
          font-size: 25px;
          color: #fff;
          margin-top: -40px;
        }
        .high-bold-3 {
          justify-content: left;
          font-family: "Itim", cursive;
          font-size: 25px;
          color: #fff;
          margin-top: -40px;
        }
      }
      .left-regular {
        width: 500px;
        height: 100px;
        display: flex;
        // align-items: center;
        justify-content: left;
        flex-direction: column;
        margin-top: 50px;
        .low-bold-1 {
          justify-content: left;
          font-family: "Jaldi", sans-serif;
          font-size: 15px;
          color: #fff;
          margin-top: 10px;
        }
        .low-bold-2 {
          justify-content: left;
          font-family: "Jaldi", sans-serif;
          font-size: 15px;
          color: #fff;
          margin-top: -20px;
        }
        .low-bold-3 {
          justify-content: left;
          font-family: "Jaldi", sans-serif;
          font-size: 15px;
          color: #fff;
          margin-top: -20px;
        }
      }
    }
    .main-section-bottom-left {
      display: flex;
      position: absolute;
      margin-top: 300px;
      margin-left: 100px;
      button {
        width: 130px;
        height: 45px;
        background-color: #f26c4f;
        outline: none;
        border-radius: 7px;
        border: none;
        cursor: pointer;
        p {
          color: #fff;
        }
      }
    }
    .main-section-top-right {
      display: flex;
      flex-direction: row;
      position: absolute;
      top: 10px;
      left: 100px;
      .right-triangle {
        width: 0;
        height: 0;
        border-top: 100px solid #2f2f2f;
        border-left: 100px solid transparent;
        transform: rotate(-80deg);
        position: absolute;
        top: 180px;
        left: 750px;
      }
      .right-circle {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background-color: #0afcb8;
        position: absolute;
        top: 235px;
        left: 800px;
        box-shadow: 0 0 10px rgba(10, 252, 184, 1);
      }
    }
    .main-section-bottom-right {
      display: flex;
      flex-direction: row;
      position: absolute;
      right: 150px;
      bottom: 10px;
      .box-circle-left {
        // background-color: #f26c4f;
        display: block;
        width: 296px;
        height: 250px;
        background-color: #2f2f2f;
        border-radius: 150px 100px 120px 100px / 120px 150px 70px 70px;
      }
      .box-circle-right {
        // background-color: #f26c4f;
        display: block;
        width: 256px;
        height: 210px;
        background-color: #f26c4f;
        border-radius: 150px 100px 120px 100px / 120px 150px 50px 50px;
        margin-left: -50px;
        margin-top: 40px;
      }
      img {
        margin-left: -400px;
        margin-top: -150px;
        width: 400px;
        height: 400px;
        border-radius: 0px 0px 100px 100px;
      }
    }
  }
}

// ****************************************
.about-section {
  position: absolute;
  top: 100vh;
  height: 90vh;
  margin-top: 100px;
  .about-section-top {
    display: flex;
    align-items: left;
    flex-direction: column;
    width: 600px;
    height: 400px;
    margin-left: 100px;
    .about-title {
      height: 50px;
      display: flex;
      align-items: left;
      flex-direction: row;
      p {
        font-family: "Itim", cursive;
        font-size: 15px;
        color: #f26c4f;
        text-decoration: underline;
      }
      span {
        width: 70px;
        height: 3px;
        background-color: #f26c4f;
        margin-top: 23px;
        margin-left: 10px;
        border-radius: 3px;
      }
    }
    .about-top-title {
      width: 600px;
      height: 160px;
      .about-top-title-1 {
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: left;
        margin-top: 10px;
        p {
          font-family: "Itim", cursive;
          font-size: 18px;
          color: #fff;
        }
      }
      .about-top-title-2,
      .about-top-title-3,
      .about-top-title-4 {
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: left;
        margin-top: -10px;
        p {
          font-family: "Itim", cursive;
          font-size: 18px;
          color: #fff;
        }
      }
    }
    .about-bottom-title {
      width: 600px;
      height: 160px;
      .about-bottom-title-1 {
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: left;
        margin-top: 10px;
        p {
          font-family: "Inter", sans-serif;
          font-size: 13px;
          color: #fff;
        }
      }
      .about-bottom-title-2,
      .about-bottom-title-3,
      .about-bottom-title-4 {
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: left;
        margin-top: -10px;
        p {
          font-family: "Inter", sans-serif;
          font-size: 13px;
          color: #fff;
        }
      }
    }
  }
  .about-section-bottom {
    width: 1000px;
    height: 200px;
    margin-left: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: -50px;
    .about-top-line {
      position: absolute;
      right: 180px;
      display: flex;
      flex-direction: row;
      margin-top: -20px;
      .about-top-line-1 {
        width: 140px;
        height: 150px;
        border-top: 2px solid #fff;
        border-left: 2px solid #fff;
        transform: rotate(50deg);
        border-radius: 100px 0px 100px 0px / 100px 0px 0px 50px;
        margin-top: -145px;
        margin-right: -5px;
      }
      .about-top-line-traingle {
        color: #f26c4f;
        transform: rotate(50deg);
        margin-top: -82px;
        margin-left: 7px;
      }
    }
    .about-middle-boxes {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: row;
      .about-box1,
      .about-box2,
      .about-box3 {
        width: 200px;
        height: 110px;
        background-color: #2f2f2f;
        border-radius: 7px;
        box-shadow: 0 0 4px #e5e5e5;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-left: 120px;
        p {
          font-family: "Itim", cursive;
          font-size: 15px;
          color: #fff;
        }
        span {
          width: 50px;
          height: 4px;
          background-color: #f26c4f;
          // margin-top: 23px;
          // margin-left: 10px;
          border-radius: 3px;
        }
      }
      .about-box2 {
        margin-top: -50px;
      }
    }
    .about-bottom-line {
      position: absolute;
      left: 420px;
      display: flex;
      flex-direction: row;
      margin-top: 250px;
      .about-bottom-line-1 {
        width: 140px;
        height: 150px;
        border-bottom: 2px solid #fff;
        border-right: 2px solid #fff;
        transform: rotate(40deg);
        border-radius: 0px 0px 100px 0px / 0px 0px 100px 0px;
        margin-top: -145px;
        margin-right: -5px;
      }
      .about-bottom-line-traingle {
        color: #f26c4f;
        transform: rotate(-55deg);
        margin-top: -112px;
        margin-left: 70px;
      }
    }
  }
  .about-section-right {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: -120px;
    right: 800px;
    .about-right-triangle {
      width: 0;
      height: 0;
      border-top: 100px solid #2f2f2f;
      border-left: 100px solid transparent;
      transform: rotate(-20deg);
      position: absolute;
      top: 180px;
      left: 750px;
    }
    .about-right-circle {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background-color: #0afcb8;
      position: absolute;
      top: 235px;
      left: 750px;
      box-shadow: 0 0 10px rgba(10, 252, 184, 1);
    }
  }
}
