.projects-section {
  position: absolute;
  top: 300vh;
  height: 90vh;
  width: 1440px;
  margin-top: 100px;
  .projects-main-section {
    width: 100%;
    height: 90%;
    margin-top: 50px;
    display: grid;
    grid-template-columns: auto auto auto;
    .projects-item-1 {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 220px;
      height: 250px;
      // margin-left: 50px;
      border-radius: 7px;
      background-color: #2f2f2f;
      box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
      padding: 10px;

      .projects-item-1-1 {
        width: 100%;
        height: 30%;
        display: flex;
        align-items: center;
        justify-content: center;
        p {
          color: #fff;
          font-size: 40px;
        }
      }
      .projects-item-1-2 {
        width: 100%;
        height: 10%;
        display: flex;
        align-items: center;
        justify-content: center;
        p {
          color: #f26c4f;
          font-family: "item", cursive;
          font-size: 13px;
        }
      }
      .projects-item-1-3 {
        width: 100%;
        height: 40%;
        display: flex;
        align-items: center;
        justify-content: center;
        p {
          color: #fff;
          font-family: "Jaldi", sans-serif;
          font-size: 10px;
          margin-left: 5px;
        }
      }
      .projects-item-1-4 {
        width: 100%;
        height: 20%;
        display: flex;
        align-items: center;
        justify-content: center;
        button {
          width: 100px;
          height: 30px;
          border-radius: 4px;
          border: none;
          cursor: pointer;
          background-color: #f26c4f;
          color: #fff;
        }
      }
    }
  }
}

// *****************************************

.skills-section {
  position: absolute;
  top: 400vh;
  height: 90vh;
  width: 1440px;
  margin-top: 100px;
  .skills-main-section {
    // border: 1px solid #fff;
    width: 100%;
    height: 90%;
    margin-top: 50px;
    display: grid;
    grid-template-columns: auto auto auto auto auto auto auto;
    .skills-bar-1 {
      // border: 1px solid #fff;
      width: 120px;
      height: 120px;
      border-radius: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #2f2f2f;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
      padding: 10px;
      .skills-bar-1-circle {
        border: 2px solid #f26c4f;
        border-style: dashed;
        width: 105px;
        height: 105px;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        // padding: 10px;
        p {
          font-family: "JetBrains Mono", monospace;
          color: #0afcb8;
        }
      }
    }
  }
}

// ********************************
.contact-section {
  position: absolute;
  top: 500vh;
  height: 90vh;
  width: 1440px;
  margin-top: 100px;
  .contact-main-section {
    // border: 1px solid #fff;
    width: 100%;
    height: 90%;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .contact-top {
      width: 800px;
      height: 300px;
      background-color: #2f2f2f;
      border-radius: 10px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .contact-top-1 {
        width: 100%;
        height: 20%;
        display: flex;
        align-items: center;
        justify-content: center;
        p {
          color: #fff;
          font-family: "item", cursive;
          font-size: 25px;
        }
      }
      .contact-top-2 {
        width: 100%;
        height: 40%;
        display: grid;
        grid-template-columns: auto auto auto;
        align-items: center;
        justify-content: center;
        .contact-top-2-1,
        .contact-top-2-2,
        .contact-top-2-3 {
          width: 210px;
          height: 100%;
          margin-left: 30px;
          margin-right: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          p {
            color: #0afcb8;
            font-size: 25px;
          }
          span {
            color: #fff;
            font-size: 10px;
            font-family: "item", cursive;
          }
        }
      }
      .contact-top-3 {
        width: 100%;
        height: 40%;
        display: flex;
        flex-direction: row;
        .contact-top-3-1 {
          width: 200px;
          height: 40px;
          position: relative;
          background: #f26c4f;
          margin-top: 80px;
          border-bottom-left-radius: 10px;
          &:before {
            content: "";
            position: absolute;
            right: -20px;
            bottom: 0;
            width: 0;
            height: 0;
            border-left: 20px solid #f26c4f;
            border-top: 20px solid transparent;
            border-bottom: 20px solid transparent;
          }
        }
        .contact-top-3-2 {
          width: 150px;
          height: 45px;
          border-radius: 10px;
          margin-left: 150px;
          margin-top: 60px;
          background-color: #f26c4f;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          p {
            color: #fff;
            font-family: "item", cursive;
          }
        }
      }
    }
    .contact-bottom {
      // border: 1px solid #fff;
      height: 150px;
      position: absolute;
      bottom: 0;
      width: 100%;
      background-color: #2f2f2f;
      .contact-bottom-1 {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 50px;
        top: 50px;
        p {
          color: rgba(255, 255, 255, 0.7);
          font-family: "Ropa Sans", sans-serif;
          font-size: 20px;
        }
      }
      .contact-bottom-2 {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 50px;
        top: 50px;
        p {
          color: rgba(255, 255, 255, 0.7);
          font-size: 30px;
          margin-left: 20px;
        }
      }
    }
  }
}

// **************************************************

@media screen and (max-width: 400px) {
  .main-page {
    width: 375px;
    height: 100vh;

    // **************************************
    .top-section {
      width: 375px;
      .top-menu {
        width: 100%;
        .top-menu-item-box {
          display: none;
        }
        .top-menu-item-all {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          .top-menu-item-0-icon,
          .top-menu-item-1,
          .top-menu-item-2,
          .top-menu-item-3,
          .top-menu-item-4,
          .top-menu-item-5,
          .top-menu-item-6 {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 375px;
            height: 40px;
            margin-left: 0px;
          }
        }
      }
    }
    .main-section {
      display: flex;
      align-items: center;
